<template>
	<view>
		<map style="height: 800rpx;width:100%;"></map>
		<view style="background:#fff;padding:20rpx;margin-top:20rpx">
			<view style="margin-bottom: 10rpx;">选择片区：</view>
			<uni-data-checkbox multiple v-model="checkedArea" :localdata="areaOptions" @change="change"></uni-data-checkbox>
		</view>
		<view class="flex flex-direction" style="padding:30rpx"><button class="cu-btn bg-blue lg" @click="confirm">确 定</button></view>
	</view>
</template>

<script>
import { taskAPI } from '@/api/index.js';
export default {
	data() {
		return {
			areaOptions: [],
			checkedArea: []
		};
	},
	onLoad() {
		this.getMidGroupArea();
	},
	onShow(){
		let area = uni.getStorageSync('checkedArea')
		if(area){
			this.checkedArea = area.split(',')
		}
	},
	methods: {
		getMidGroupArea() {
			//查询中队下所有片区
			taskAPI.getMidGroupArea().then(res => {
				this.areaOptions = res.data.map(item => {
					return {
						text: item,
						value: item
					};
				});
			});
		},
		change(val) {
			console.log(val);
		},
		confirm() {
			//确定
			if (this.checkedArea.length == 0) {
				uni.showToast({
					icon: 'none',
					title: '请选择片区'
				});
			} else {
				let str = this.checkedArea.join(',');
				uni.setStorageSync('checkedArea', str);
				uni.navigateBack()
			}
		}
	}
};
</script>

